<template>
  <div id="glimpse" style="margin:0 auto;">
    <div class="glimpse-container" v-if="this.$store.state.wordbook.slug && !this.$store.state.wordbook.currentWordbookDone">
      <v-container fluid="fluid">
        <v-layout row>
          <v-flex xs6 offset-xs3 class="text-card">
            <v-card class="blue darken-1 white--text">
              <v-card-text v-if="currentWordLength === 'long'" @click="speakPronunciation" class="pointer display-3 white--text text--darken-4 small-text" style="text-align: center; padding-top: 2rem;">
                {{ currentWord.word }}
              </v-card-text>
              <v-card-text v-if="currentWordLength === 'middle'" @click="speakPronunciation" class="pointer display-3 white--text text--darken-4 middle-text" style="text-align: center; padding-top: 2rem;">
                {{ currentWord.word }}
              </v-card-text>
              <v-card-text v-if="currentWordLength === 'short'" @click="speakPronunciation" class="pointer display-3 white--text text--darken-4 big-text" style="text-align: center; padding-top: 2rem;">
                {{ currentWord.word }}
              </v-card-text>

              <v-card-text class="headline" style="text-align: center; padding-bottom: 2.5rem;">
                {{ currentWord.exp }}
              </v-card-text>

              <v-card-row style="padding-top: 0.8rem; padding-bottom: 0.8rem;" class="white">
                <v-flex xs2></v-flex>
                <v-flex xs4 style="position: relative">
                  <v-btn light large info v-bind:loading="isButtonDisabled"
                          v-on:click.native="forgotAction()" v-bind:disabled="isButtonDisabled"
                          style="position: absolute; right: 0px; margin-top: -22px;">
                      不记得
                    <span slot="loader">不记得</span>
                  </v-btn>
                </v-flex>
                <v-flex xs4>
                  <v-btn light large success v-bind:loading="isButtonDisabled"
                          v-on:click.native="rememberAction()" v-bind:disabled="isButtonDisabled">
                      记得
                    <span slot="loader">记得</span>
                  </v-btn>
                </v-flex>
                <v-flex xs2></v-flex>
              </v-card-row>
            </v-card>
          </v-flex>
        </v-layout>
      </v-container>
      <audio ref="pronunciation" v-if="pronunciationUrl.length && autoread" :src="pronunciationUrl" autoplay></audio>
      <audio ref="pronunciation" v-if="pronunciationUrl.length && !autoread" :src="pronunciationUrl"></audio>
    </div>

    <div class="glimpse-container" v-else-if="this.$store.state.wordbook.slug && this.$store.state.wordbook.currentWordbookDone">
      <v-card-row height="300px" class="pa-5 grey lighten-1">
        <div class="display-1 white--text text-xs-center">背完了啊老铁，牛逼啊，再来一次？</div>
      </v-card-row>
    </div>
      <div class="glimpse-container" v-else>
        <v-card-row height="300px" class="pa-5 grey lighten-1">
          <div class="display-1 white--text text-xs-center">先选个单词书啊老铁</div>
        </v-card-row>
      </div>
  </div>
</template>

<script>
  import shared from '@/shared.js'
  import settings from '@/utils/settings'

  export default {
    name: "glimpse",
    computed: {
      isButtonDisabled() {
        return this.$store.state.wordbook.loadingData;
      },
      currentWord() {
        const current = this.$store.state.wordbook.currentWord;
        if (current) {
          return current.val;
        } else {
          return {
            word: "",
            exp: ""
          }
        }
      },
      currentWordLength() {
        try {
          if (this.currentWord.word.length > 12) {
            return 'long'
          } else if (this.currentWord.word.length > 6) {
            return 'middle'
          } else {
            return 'short'
          }          
        } catch (e) {
          return 'long'
        }
      },
      pronunciationUrl() {
        return 'http://fanyi.baidu.com/gettts?lan=en&text=' + this.currentWord.word + '&spd=3&source=web'
      }
    },
    methods: {
      rememberAction(event) {
        console.log('rememberAction invoked');
        this.$store.commit('APPEND_WORD_REMEMBERED');
        this.$store.dispatch('getNextWord');
      },
      forgotAction(event) {
        console.log('forgotAction invoked');
        const wordbook = this.$store.state.wordbook;
        this.$store.dispatch('addForgotWord', {
          slug: wordbook.slug,
          offset: wordbook.currentWord.offset
        }).then((function () {
          this.$store.dispatch('getNextWord');
        }).bind(this));
      },
      handleKeyUpEvent(event) {
        if (event.keyCode == 90) {
          // key Z
          this.rememberAction();
        } else if (event.keyCode == 191) {
          // key /
          this.forgotAction();
        }
      },
      speakPronunciation() {
        if (this.clicksound) {
          let playPromise = this.$refs.pronunciation.play();
          if (playPromise !== undefined) {
            playPromise.catch(function (err) {
              setTimeout(function() {
                this.$refs.pronunciation.play();
              }.bind(this), 500);
            })
          }          
        }
      }
    },
    created() {
      // get pieces
      const state = this.$store.state.wordbook;
      const settings_state = this.$store.state.settings;
      console.log('glimpse/frame.vue created', settings_state.loadingStatus.userStatusData)
      if (!settings_state.loadingStatus.userStatusData) {
        this.$store.dispatch('getUserStatus').then((function () {
          // 设置 glimpse 的状态
          this.$store.dispatch('setState', {slug: settings_state.wordbookStatus.slug})
          if (!state.currentWord) {
            // 更新一下数据
            this.$store.dispatch('getNextWord');
          }
        }).bind(this))
      } else {
        // 设置glimpse状态
        this.$store.dispatch('setState', {slug: settings_state.wordbookStatus.slug})

        if (!state.currentWord) {
          // 更新一下数据
          this.$store.dispatch('getNextWord');
        }

      }

    },
    mounted() {
      shared.eventBus.$emit('nav-changed', "glimpse");
      window.addEventListener('keyup', this.handleKeyUpEvent);
      if (settings.getSettingitem('clicksound') === null) {
        settings.saveSettingitem('clicksound', false);
      }
      if (settings.getSettingitem('autoread') === null) {
        settings.saveSettingitem('autoread', false);
      }
      this.clicksound = settings.getSettingitem('clicksound');
      this.autoread = settings.getSettingitem('autoread');
    },
    destroyed() {
      window.removeEventListener('keyup', this.handleKeyUpEvent);
    },
    data() {
      return {
        loader: null,
        clicksound: false,
        autoread: false
      }
    },

    // beforeRouteEnter(to, from, next) {
    //   getPost(to.params.id, (err, post) => {
    //     next(vm => vm.setData(err, post))
    //   })
    // },
    // // when route changes and this component is already rendered,
    // // the logic will be slightly different.
    // beforeRouteUpdate(to, from, next) {
    //   this.post = null
    //   getPost(to.params.id, (err, post) => {
    //     this.setData(err, post)
    //     next()
    //   })
    // },

  }
</script>

<style scoped>
  .pointer {
    cursor: pointer;
  }
  @media screen and (max-width: 640px) {
    .text-card {
      min-width: 85%;
      margin-left: 8% !important;
    }
    .big-text {
      font-size: 56px;
    }
    .middle-text {
      font-size: 36px;
      margin-bottom: 20px;
    }
    .small-text {
      font-size: 32px;
      margin-bottom: 28px;
    }
  }
</style>



