<template>
  <div id="settings-advanced">
    <v-list two-line subheader>
      <v-subheader>发音</v-subheader>
      <v-list-item>
        <v-list-tile avatar>
          <v-list-tile-action>
            <v-checkbox v-model="clicksound"></v-checkbox>
          </v-list-tile-action>
          <v-list-tile-content>
            <v-list-tile-title>点击发音</v-list-tile-title>
            <v-list-tile-sub-title>允许点击单词发音</v-list-tile-sub-title>
          </v-list-tile-content>
        </v-list-tile>
      </v-list-item>
      <v-list-item>
        <v-list-tile avatar>
          <v-list-tile-action>
            <v-checkbox v-model="autoread"></v-checkbox>
          </v-list-tile-action>
          <v-list-tile-content>
            <v-list-tile-title>自动发音</v-list-tile-title>
            <v-list-tile-sub-title>允许单词切换后自动发音</v-list-tile-sub-title>
          </v-list-tile-content>
        </v-list-tile>
      </v-list-item>
    </v-list>
  </div>
</template>

<script>
  import settings from '@/utils/settings'

  export default {
    data() {
      return {
        clicksound: false,
        autoread: false
      }
    },
    watch: {
      clicksound: function () {
        settings.saveSettingitem('clicksound', this.clicksound)
      },
      autoread: function () {
        settings.saveSettingitem('autoread', this.autoread)
      }
    },
    mounted () {
      if (settings.getSettingitem('clicksound') === null) {
        settings.saveSettingitem('clicksound', false);
      }
      if (settings.getSettingitem('autoread') === null) {
        settings.saveSettingitem('autoread', false);
      }
      this.clicksound = settings.getSettingitem('clicksound');
      this.autoread = settings.getSettingitem('autoread');
    }
  }
</script>



