<template>
  <div id="glimpse" style="margin:0 auto;">
    <div class="glimpse-container" v-if="this.$store.state.wordbook.wordsForgotten.value > 0">
      <v-container fluid="fluid">
        <v-layout row>
          <v-flex xs6 offset-xs3 class="text-card">
            <v-card class="green lighten-1 white--text" style="border-radius: 5px">
              <v-card-text v-if="currentWordLength === 'long'" @click="speakPronunciation" class="pointer display-3 white--text text--darken-4 small-text" style="text-align: center; padding-top: 2rem;">
                {{ currentWord.word }}
              </v-card-text>
              <v-card-text v-if="currentWordLength === 'middle'" @click="speakPronunciation" class="pointer display-3 white--text text--darken-4 middle-text" style="text-align: center; padding-top: 2rem;">
                {{ currentWord.word }}
              </v-card-text>
              <v-card-text v-if="currentWordLength === 'short'" @click="speakPronunciation" class="pointer display-3 white--text text--darken-4 big-text" style="text-align: center; padding-top: 2rem;">
                {{ currentWord.word }}
              </v-card-text>

              <v-card-text class="headline" style="text-align: center; padding-bottom: 2.5rem;">
                {{ currentWord.exp }}
              </v-card-text>

              <v-card-row style="padding-top: 0.8rem; padding-bottom: 0.8rem;" class="white">
                <v-flex xs2></v-flex>
                <v-flex xs4 style="position: relative">
                  <v-btn light large info v-bind:loading="isButtonDisabled"
                          v-on:click.native="forgotAction()" v-bind:disabled="isButtonDisabled"
                          style="position: absolute; right: 0px; margin-top: -22px;">
                      不记得
                    <span slot="loader">不记得</span>
                  </v-btn>
                </v-flex>
                <v-flex xs4>
                  <v-btn light large success v-bind:loading="isButtonDisabled"
                          v-on:click.native="rememberAction()" v-bind:disabled="isButtonDisabled">
                      记得
                    <span slot="loader">记得</span>
                  </v-btn>
                </v-flex>
                <v-flex xs2></v-flex>
              </v-card-row>
            </v-card>
          </v-flex>
        </v-layout>
      </v-container>
      <audio ref="pronunciation" v-if="pronunciationUrl.length && autoread" :src="pronunciationUrl" autoplay></audio>
      <audio ref="pronunciation" v-if="pronunciationUrl.length && !autoread" :src="pronunciationUrl"></audio>
    </div>

    <div class="glimpse-container" v-else>
      <v-card-row height="calc(100vh - 4rem)" class="pa-5 grey lighten-1">
        <div class="display-1 white--text text-xs-center">牛逼啊老铁，没货</div>
      </v-card-row>
    </div>
  </div>
</template>

<script>
  import shared from '@/shared.js'
  import settings from '@/utils/settings'

  export default {
    name: "review",
    computed: {
      isButtonDisabled() {
        return this.$store.state.review.loadingData;
      },
      currentWord() {
        const current = this.$store.state.review.currentWord;
        if (current) {
          return current;
        } else {
          return {
            word: "",
            exp: ""
          }
        }
      },
      currentWordLength() {
        try {
          if (this.currentWord.word.length > 12) {
            return 'long'
          } else if (this.currentWord.word.length > 6) {
            return 'middle'
          } else {
            return 'short'
          }          
        } catch (e) {
          return 'long'
        }
      },
      pronunciationUrl() {
        return 'http://fanyi.baidu.com/gettts?lan=en&text=' + this.currentWord.word + '&spd=3&source=web'
      }
    },
    methods: {
      rememberAction(event) {
        console.log('rememberAction invoked');
        const review_state = this.$store.state.review;

        if (this.$store.state.wordbook.wordsForgotten.value == 0) {
          console.log('wordsForgotten.value == 0, ignore event.')
          return
        }

        this.$store.dispatch('ackWordAction', review_state.currentWord.offset).then((function () {
          this.$store.dispatch('getBacklogWord');
        }).bind(this));
      },

      forgotAction(event) {
        console.log('forgotAction invoked');
        if (this.$store.state.wordbook.wordsForgotten.value == 0) {
          console.log('wordsForgotten.value == 0, ignore event.')
          return
        }

        const review_state = this.$store.state.review;

        this.$store.dispatch('addForgotWord', {
          slug: review_state.currentWord.slug,
          offset: review_state.currentWord.offset
        }).then((function () {
          this.$store.dispatch('getBacklogWord');
        }).bind(this));
      },

      handleKeyUpEvent(event) {
        if (event.keyCode == 90) {
          // key Z
          this.rememberAction();
        } else if (event.keyCode == 191) {
          // key /
          this.forgotAction();
        }
      },
      speakPronunciation() {
        if (this.clicksound) {
          let playPromise = this.$refs.pronunciation.play();
          if (playPromise !== undefined) {
            playPromise.catch(function (err) {
              setTimeout(function() {
                this.$refs.pronunciation.play();
              }.bind(this), 500);
            })
          }
        }
      }
    },

    created() {
      // get pieces
      const state = this.$store.state.review;

      if (!state.currentWord.word) {
        // 更新一下数据
        this.$store.dispatch('getBacklogWord');
      }
    },
    mounted() {
      shared.eventBus.$emit('nav-changed', "review");
      window.addEventListener('keyup', this.handleKeyUpEvent);
      if (settings.getSettingitem('clicksound') === null) {
        settings.saveSettingitem('clicksound', false);
      }
      if (settings.getSettingitem('autoread') === null) {
        settings.saveSettingitem('autoread', false);
      }
      this.clicksound = settings.getSettingitem('clicksound');
      this.autoread = settings.getSettingitem('autoread');
    },
    destroyed() {
      window.removeEventListener('keyup', this.handleKeyUpEvent);
    },
    data() {
      return {
        loader: null,
        clicksound: false,
        autoread: false
      }
    }
  }
</script>

<style scoped>
  .pointer {
    cursor: pointer;
  }
  @media screen and (max-width: 640px) {
    .text-card {
      min-width: 80%;
      margin-left: 10% !important;
    }
    .big-text {
      font-size: 56px;
    }
    .middle-text {
      font-size: 36px;
      margin-bottom: 20px;
    }
    .small-text {
      font-size: 32px;
      margin-bottom: 28px;
    }
  }
</style>



